body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:#1a1a1d}p,ul,h3{color:#f0f8ff;list-style-type:none}nav{background:red}.movie-wrapper{padding:3rem}.movie{position:relative;display:inline-block;-webkit-transition:all .3s ease-in-out;transition:all .3s ease-in-out;border:2px solid #1a1a1d;cursor:pointer}.gray-scale{-webkit-filter:grayscale(100%);filter:grayscale(100%)}.show{display:block;background:rgba(0,0,0,.8)}.modal-dialog{max-width:90vw !important}.modal-content{background:#c3073f;margin:auto;height:90vh;background-size:cover !important;background-position:center !important}.modal-content::before{content:"";position:absolute;width:100.3%;height:100%;top:0;left:-1px;background:radial-gradient(circle, rgba(0, 0, 0, 0) 0%, black 100%);opacity:1}.close{cursor:pointer;position:absolute;top:2%;right:2%;font-size:50px;color:#fff;z-index:20}.close:hover{color:#c3073f}.movie:after{content:"";position:absolute;left:0;width:100%;height:100%;box-shadow:0px 20px 40px 0px #1a1a1d;-webkit-transition:opacity .3s ease-in-out;transition:opacity .3s ease-in-out}.movie:hover{z-index:200;-webkit-transform:scale(1.3, 1.3);transform:scale(1.3, 1.3);border:none}.title{position:absolute;z-index:-1;top:50%;color:#fff}.videoWrapper{position:relative;padding-bottom:56.25%;padding-top:25px;height:0}.videoWrapper iframe{position:absolute;top:0;left:0;width:100%;height:100%;border:none;margin:0;box-shadow:0 0 20px 10px #050505}.info{z-index:2;position:relative}.info img{width:30%}.trailer{position:relative}.fade-layer{position:absolute;width:250px;height:100%;left:0;background:#fff;z-index:3;background:-webkit-gradient(linear, left top, right top, from(white), to(black));background:linear-gradient(90deg, white 0%, black 100%);pointer-events:none}.slide-show{max-width:100%;height:40vw;padding:0;position:relative;display:flex;justify-content:center;margin:0;overflow:hidden}.slide-show .slide-btn{color:#fff;display:flex;align-items:center;justify-content:center;font-size:60px;position:absolute;width:10%;height:100%;background:rgba(0,0,0,.8);opacity:.6;cursor:pointer;-webkit-transition:.5s;transition:.5s;z-index:3}.slide-show .prev{left:0}.slide-show .prev:hover{opacity:1}.slide-show .next{right:0}.slide-show .next:hover{opacity:1}.slide{height:100%;width:400%;-webkit-transition:.5s;transition:.5s;position:absolute;left:10%}.slide::before{content:"";position:absolute;width:100%;height:50%;background:-webkit-gradient(linear, left top, left bottom, from(rgba(0, 5, 255, 0)), to(black));background:linear-gradient(180deg, rgba(0, 5, 255, 0) 0%, black 100%);bottom:0;pointer-events:none}.slide .hero{width:80%;cursor:pointer}.slide .hero img{width:100%;object-fit:cover}.slide .hero h3{max-width:15%;position:absolute;bottom:0;opacity:0;-webkit-transition:.5s;transition:.5s}.slide .hero:hover h3{opacity:1}/*# sourceMappingURL=App.css.map */

